import { createApplicationSwitcher } from '@sas/nova-core/app-switcher/ApplicationSwitcher';
import AppRegistryService from '@sas/nova-core/app-switcher/services/AppRegistryService';
import { addBundle } from '../../locale/createUseTextLocalizer';
import { getTextLocalizer, Localizer } from '../../locale/useTextLocalizer';

import bundle from './NovaComponents-NovaApplicationSwitcher-gui-icu.properties';

addBundle(bundle);

const getLogoffUrl = (): string => {
  const origin = window.location.origin;
  const pathname = window.location.pathname;
  const secondSlashIndex = pathname.indexOf('/', 1);
  const contextPath = pathname.substring(0, secondSlashIndex);
  const logoutPath = '/logout';
  return origin + contextPath + logoutPath;
};

let localize: Localizer;
getTextLocalizer('NovaComponents-NovaApplicationSwitcher-gui-icu').then((localizer) => {
  localize = localizer;
});

const getUnsavedChangesLocalizedString = (): string => {
  return localize('unsavedChanges.msg');
};

export const applicationSwitcher = createApplicationSwitcher({
  applicationService: new AppRegistryService(),
  getLogoffUrl: getLogoffUrl,
  getUnsavedChangesLocalizedString: getUnsavedChangesLocalizedString,
});
